footer{
	display: grid;
	grid-template-columns: repeat(3, 1fr) max-content;
	background-color: @footer;
	color: white;
	h3{
		color:white;
		text-transform: uppercase;
	}
	*{
		grid-row:1/ span 2;
	}
	.subscribe{
		grid-column: 4/span 1;
		grid-row:2/ span 1;
	}
	.social{
		grid-row:1/ span 1;

	}
	small{
		grid-row:3/ span 1;
	}
	.payments{
		grid-row:4/ span 1;

	}
}